/*============================

============================*/
.sidebar {
  &-layout {
    .row {
      justify-content: space-between;
    }

    .col-md-8 {
      flex: 0 0 68%;
      max-width: 68%;
      @include media-breakpoint-down(md) {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }

    .col-md-4 {
      flex: 0 0 28%;
      max-width: 28%;
      @include media-breakpoint-down(md) {
        flex: 0 0 100%;
        max-width: 100%;
      }
    }
  }

  .md_topTitle {
    font-size: 1.125rem;
    margin-bottom: 1rem;
    padding: 0;
    font-weight: lighter;
  }
}

.sidebar {
  // sidebar 
  &__search {
    .search {
      &__form {
        border: 1px solid $success;

        table {
          .select-wrap {
            position: relative;
          }

          tr {
            display: inline-grid;
            width: 100%;
            margin: 0;
            padding: 0;
            //  
            th {
              width: 100%;
              background: $success;
              font-family: "", "Yu Mincho", "", "YuMincho", " Pro W2", "Hiragino Mincho Pro", "HiraMinProN-W2", "HGSE", " ", "MS PMincho";
              font-size: 0.9rem;
              font-weight: lighter;
              padding: 7px 10px;
            }
            //  
            &.content {
              width: 100%;
              padding: 1rem 10px;

              td {
                margin-bottom: 8px;

                input {
                  font-size: 0.9rem;
                }
                //  
                input[type="checkbox"] {
                  position: relative;
                  width: 15px;
                  height: 15px;
                  top: -6px;
                  margin-right: 1px;
                }

                .check-list {
                  display: flex;
                  justify-content: flex-start;
                  flex-wrap: wrap;

                  &-item {
                    margin-right: 1rem;
                    margin-bottom: 6px;
                  }
                }
                //  
                select {
                  position: relative;
                  padding: 6px 10px 10px;
                  width: 100%;
                  border: 1px solid #ccc;
                  color: #555;
                  background: $info;
                  font-size: 0.9rem;
                }
                //  
                p {
                  margin-bottom: 9px;
                }
              }

              label {
                margin: 0;
                padding: 0;
                font-size: 0.85rem;
                display: flex;
                align-items: center;
              }
            }
          }
        }
      }
    }
  }

  &__blog {
    .archive__inner {
      position: relative;
      align-items: center;
      padding: 0 0 5px;

      &::after {
        content: "01";
        position: absolute;
        top: 9px;
        right: 4px;
        color: $info;
        font-size: 60px;
        font-family: "", "Yu Mincho", "", "YuMincho", " Pro W2", "Hiragino Mincho Pro", "HiraMinProN-W2", "HGSE", " ", "MS PMincho";
        z-index: -1;
      }

      &:nth-child(2) {
        &::after {
          content: "02";
        }
      }

      &:nth-child(3) {
        &::after {
          content: "03";
        }
      }

      &:nth-child(4) {
        &::after {
          content: "04";
        }
      }

      &:nth-child(5) {
        &::after {
          content: "05";
        }
      }

      .img-wrap {
        padding: 0;
        height: 87px;
      }

      .txt-wrap {
        padding-right: 0;
      }

      h3 {
        font-size: 0.875rem;
        margin: 0 0 5px;
        font-weight: bold;
      }
    }
  }

  &__more {
    .guide__more__list__inner {
      display: block;
      width: 100%;
    }
  }
}
/*============================

============================*/
.sidebar {
  &__more {
    .md_topTitle {
      line-height: 2.5;
      margin-bottom: 0 !important;
      @include media-breakpoint-down(md) {
        margin-bottom: 1rem;
      }

      span {
        position: relative;
        font-size: 0.8rem;
        padding: 13px 2rem;
        border-radius: 2rem;
        line-height: 3;
        background: $success;
        @include media-breakpoint-down(md) {
          font-size: 1.125rem;
        }

        &::after {
          content: "";
          position: absolute;
          left: calc(50% - 9px);
          bottom: -2px;
          width: 18px;
          height: 18px;
          transform: rotate(45deg);
          background: $success;
          z-index: -1;
        }
      }
    }

    .guide {
      &__more {
        &__list {
          &__inner {
            padding: 4.2rem 0;
            border: none;

            p {
              font-size: 1.125rem;
              font-weight: normal;
              margin: 0;
            }
          }
        }
      }
    }
  }

  &.nomal {
    .sidebar {
      &__blog {
        .archive__inner {
          position: relative;
          align-items: center;
          padding: 0 0 5px;

          &::after {
            display: none;
          }
        }
      }
    }
  }
}
